.control-2
{
  position: relative;
  left: calc(50% - 12px);
  width: 55px;
  /* height: 200px; */
  display: flex;
  justify-content: center;
  

  /* margin-top: 70px; */
  /* margin-left: 146px; */
  transform: rotate(90deg);
  /* border: 1px solid green; */
  pointer-events: none;
  margin: 0px 10px;

}
.pl2-slider-input {
  transform: rotate(-90deg);
  position: absolute;
  top: 91px;
  left: -25px;
  width: 100px;
  opacity: .0;
  pointer-events: all;
}

div.control-2 span.eq
{
  position: relative;
  top: 50px;
  left: 22px;
  /* width: 40px; */
  width: 20px;
  /* height: 200px; */
  height: 100px;
  display: inline-block;
  border-radius: 100px;
  border: 1px solid rgba(255,255,255,0.05);
  /* border: 1px solid rgba(255,255,255); */
  margin-right: 45px;
  box-shadow: inset 0 0 65px rgba(0,0,0,0.4),
              inset 0 10px 15px rgba(0,0,0,0.3),
              inset 0 -10px 15px rgba(0,0,0,0.3),
              0 0 5px rgba(0,0,0,0.7);
  
    
}

div.control-2 span.eq:before,
div.control-2 span.eq:after
{
  position: absolute;
  content: '';
  /* width: 10px; */
  width: 3px;
  display: inline-block;
  
  /* top: 50%; */
  /* margin-top: -80px; */
  /* left: 50%; */
  left: 35%;
  top: 10%;
  margin-left: -3px;
  border-radius: 100px;
}

div.control-2 span.eq:before
{
  position: relative;
  left: 3px;
  margin: 0px 0px 0px 0px;
  z-index: 8;
  /* height: 160px; */
  height: 80px;
  /* border: 1px solid black; */
  box-shadow: inset 0 0 15px rgba(0,0,0,0.7),
              inset 0 7px 10px -2px rgba(0,0,0,0.5),
              inset 0 -7px 10px -2px rgba(0,0,0,0.5);
}

