.pl2-volume-level {
  position: absolute;
  /* top: 70px; */

  left: 26px;
  bottom: -35px;
  z-index: 9;
  height: 37px;
  width: 2px;
  background: #05d4f4;
  margin: 0px 0px 0px 0px;

  box-shadow: inset 0 0 25px rgba(255,255,255,0.5),
              0 0 10px #05d4f4;
  /* border: 1px solid yellow; */
}

span.eq span.eq-level
{
  position: absolute;
  
  /* width: 50px; */
  width: 20px;
  /* height: 75px; */
  height: 30px;
  display: inline-block;
  background: #373542;
  left: -5px;
  bottom: 37px;
  /* margin-left: -5px; */
  margin-top: 0px;
  border-radius: 100px;
  overflow: hidden;
  z-index: 10;

  box-shadow: inset 0 5px 5px -3px rgba(255,255,255,0.4),
              inset 0 -8px 8px -3px rgba(0,0,0,0.4),
              inset 0 0 55px rgba(0,0,0,0.6),
              -6px 0 5px -5px rgba(0,0,0,0.4),
              7px 0 5px -3px rgba(0,0,0,0.4),
              0 5px 5px -4px rgba(0,0,0,0.7),
              0 -8px 7px -4px rgba(0,0,0,0.7);
}

span.eq span.eq-level:before,
span.eq span.eq-level:after
{
  position: absolute;
  content: '';
  width: 100%;
  height: 2px;
  display: inline-block;
}

span.eq span.eq-level:before
{
  top: 50%;
  margin-top: -2px;
  background: rgba(0,0,0,0.4);
}

span.eq span.eq-level:after
{
  top: 50%;
  background: rgba(255,255,255,0.1);
  box-shadow: 0 1px 3px rgba(255,255,255,0.01);
}

span.eq span.eq-level img
{
  position: absolute;
  opacity: 0.2;
}

div.control-2 span.eq-1:after
{
  height: 50px;
  margin-top: 30px;
}

div.control-2 span.eq-1 span.eq-level
{
  margin-top: 80px;
}

