#volume-knob-c {
  display: flex;
  justify-content: center;
  align-items: center;

}
.control-1
{
  position: relative;
  width: 250px;
  height: 250px;
  width: 100px;
  height: 100px;
  /* top: 0px; */
  /* margin-top: 30px; */
  /* margin-left: 120px; */
  border-radius: 100%;
  border: 7px solid #19191b;
  border-bottom: 5px solid transparent;
  
  box-shadow: 0 -1px 2px -1px rgba(255,255,255,0.2),
              inset 0 1px 2px -1px rgba(255,255,255,0.2);

              
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.control-1:before
{
  position: absolute;
  content: '-';
  width: 27px;
  height: 27px;
  width: 17px;
  height: 17px;
  display: inline-block;
  border-radius: 100%;
  text-align: center;
  line-height: 10px;
  font-size: 40px;
  font-weight: 300;
  color: #05d4f4;
  text-shadow: 0 0 10px #05d4f4;
  left: 16px;
  bottom: -10px;
  /* margin-left: 37.5px; */
  /* margin-bottom: 8.5px; */
  box-shadow: inset 0 0 25px rgba(0,0,0,1);
}

.control-1:after
{
  position: absolute;
  content: '+';
  width: 27px;
  height: 27px;
  width: 17px;
  height: 17px;
  display: inline-block;
  border-radius: 100%;
  text-align: center;
  line-height: 15px;
  font-size: 20px;
  font-weight: 400;
  color: #05d4f4;
  text-shadow: 0 0 10px #05d4f4;
  right: 16px;
  bottom: -10px;
  /* margin-right: 37.5px;
  margin-bottom: 8.5px; */

  box-shadow: inset 0 0 25px rgba(0,0,0,1);
}

.control-1 span.vol-level
{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  text-align: center;
  /* transform: rotate(-136deg); */
}

div.control-1 span.vol-level:before
{
  position: relative;
  content: '';
  width: 10px;
  height: 6px;
  width: 4px;
  height: 3px;
  display: inline-block;
  background: #05d4f4;
  top: -16px;
  border-radius: 10px;

  box-shadow: -2px 0 2px #05d4f4,
              2px 0 px #05d4f4,
              inset 0 0 6px rgba(255,255,255,0.7);
}

.volume-control
{
  position: relative;
  width: 220px;
  height: 220px;
  width: 90px;
  height: 90px;
  background: #05d4f4;
  border-radius: 100%;
  text-align: center;
  /* margin-left: 15px; */
  margin: 0px 4px 0px 4px;
  
 

  /* box-shadow: inset 0 1px 2px 2px rgba(0,0,0,0.9),
              0 0 8px rgba(5,212,244,0.5),
              0 0 5px rgba(0,0,0,0.0); */
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.volume-control .panel
{
  position: absolute;
  content: '';
  width: 85%;
  height: 85%;
  display: inline-block;
  top: 3px;
  left: 3px;
  border-radius: 100%;
  margin-top: 5%;
  margin-left: 5%;
  background: #3e3c49;
  overflow: hidden;

  box-shadow: inset 0 0 60px rgba(0,0,0,0.3),
              inset -6px -6px 8px 2px rgba(0,0,0,0.2),
              inset 6px 6px 4px 2px rgba(0,0,0,0.15),
              inset 0 0px 1px 2px rgba(0,0,0,0.9),
              inset 0 0.6px 1px 2px rgba(255,255,255,0.9);
}

div.volume-control .knob
{
  /* transform: rotate(-136deg); */
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 55%;
  height: 55%;
  display: inline-block;
  border-radius: 100%;
  margin-top: 22.5%;
  margin-left: 22.5%;
  background: #42404d;
  overflow: hidden;

  /* box-shadow: inset 0px 2px 1px -1px rgba(255,255,255,0.3),
              inset 0px -2px 1px -1px rgba(255,255,255,0.03),
              0 -2px 2px -1px rgba(0,0,0,0.3),
              0 12px 4px -1px rgba(0,0,0,0.4),
              0 20px 22px 4px rgba(0,0,0,0.7),
              inset 0 0 80px rgba(0,0,0,0.55),
              inset 0 6px 6px 0 rgba(0,0,0,0.15),
              inset 0 -14px 9px 0 rgba(255,255,255,0.05),
              0 -3px 6px -1px rgba(255,255,255,0.05); */
}
.knob-shadow{
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 55%;
  height: 55%;
  display: inline-block;
  border-radius: 100%;
  margin-top: 22.5%;
  margin-left: 22.5%;
  background: #42404d;
  overflow: hidden;

  box-shadow: inset 0px 2px 1px -1px rgba(255,255,255,0.3),
              inset 0px -2px 1px -1px rgba(255,255,255,0.03),
              0 -2px 2px -1px rgba(0,0,0,0.3),
              0 12px 4px -1px rgba(0,0,0,0.4),
              0 20px 22px 4px rgba(0,0,0,0.7),
              inset 0 0 80px rgba(0,0,0,0.55),
              inset 0 6px 6px 0 rgba(0,0,0,0.15),
              inset 0 -14px 9px 0 rgba(255,255,255,0.05),
              0 -3px 6px -1px rgba(255,255,255,0.05);
}

div.volume-control .knob:before
{
  position: absolute;
  content: '';
  width: 27px;
  height: 27px;
  width: 11px;
  height: 11px;
  display: inline-block;
  border-radius: 100%;
  right: -5px;
  margin-top: 7px;
  margin-right: 25px;

  box-shadow: inset 0 0 18px rgba(0,0,0,0.4),
              inset -1px 1px 4px rgba(0,0,0,0.5),
              -0.2px 1px 1px rgba(255,255,255,0.1);
}

div.volume-control .knob img
{
  position: absolute;
  top: 0;
  left: 0;
  margin-left: -50%;
  margin-top: -50%;
  opacity: 0.09;
  pointer-events: none;
}

div.volume-control .panel img
{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.4;
}
div.volume-control img {
  pointer-events: none;
}
